import Link from 'next/link';
import { Logo } from '../components/Logo';

export default function Home() {
  return (
    <div className="space-y-6">
      <div className="flex items-center gap-3">
        <Logo />
        <h1 className="text-2xl font-semibold">Console</h1>
      </div>
      <p className="text-gray-400">Monitor Besu (via FireFly), manage privacy groups, assets, and Solana NFTs.</p>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Link href="/orgs" className="p-4 border border-gray-800 rounded bg-gray-900 hover:bg-gray-800">
          <div className="font-medium">Organizations</div>
          <div className="text-sm text-gray-400">Access, privacy, contracts, tokens, assets, Solana, relay</div>
        </Link>
        <Link href="/network" className="p-4 border border-gray-800 rounded bg-gray-900 hover:bg-gray-800">
          <div className="font-medium">Network</div>
          <div className="text-sm text-gray-400">Validators, permissioning, peers and metrics</div>
        </Link>
        <Link href="/audit" className="p-4 border border-gray-800 rounded bg-gray-900 hover:bg-gray-800">
          <div className="font-medium">Audit</div>
          <div className="text-sm text-gray-400">Search by tenant, user, type and export</div>
        </Link>
      </div>
    </div>
  );
}

